}
.individual-weather {
  height: 110px;
  width: 225px;
  background: #FFF;
  color: #000;
  border-radius: 20px;
  font-size: 10px;
  padding: 10px;
  margin: 10px 15px;
}
.weather-details {
  display: -ms-flexbox;
  display: flex;
  padding: 0 10px;
}
.weather-title {
  width: 60%;
}
.weather-icon {
  width: 40%;
}
.weather-fa-icon {
    color: #fff;
    background-color: rgb(92, 155, 226);
    border-radius: 6px;
    font-size: 6px;
    padding: 5px;
    width: 20px;
    height: 20px;
    float: right;
    margin-top: 5px;
}
.weather-status {
  display: -ms-flexbox;
  display: flex;
  padding-left: 10px;;
}
.status-info {
  width: 33%;
  margin: 5px 10px;
}
.progress-bar {
  width: 100%;
  height: 10px;
